import { 
    SET_ACTIVE_PLOT,
    SET_ACTIVE_TAB,
    CLEAR_SESSION,
    CHANGE_PLOT_SETTINGS
} from '../actions/actionTypes'

const defaultState = {plot: undefined, tab: undefined};

const active = (state = defaultState, action) => {
    switch (action.type) {
        case SET_ACTIVE_PLOT:
            console.log("REDUX: Setting active plot...")
            var newState = {
                ...state,
                plot: action.activePlot,
            }
            console.log(newState)
            return newState
        
        case CHANGE_PLOT_SETTINGS:
            console.log("REDUX: Changing plot settings (not submitting yet)...")
            var newState = {
                ...state,
                plot: {
                    ...state.plot,
                    settings: {
                        ...state.plot.settings,
                        [action.settingKey]: action.value
                    }
                },
            }
            console.log(newState)
            return newState

        case SET_ACTIVE_TAB:
            console.log("REDUX: Setting active tab...")
            var newState = {
                ...state,
                tab: action.activeTab,
            }
            console.log(newState)
            return newState
        
        case CLEAR_SESSION:
            return defaultState
                
        default:
            return state
    }
}

export default active