import React, { Component } from 'react'
import { Row, Col} from 'react-materialize'
import ReactTooltip from "react-tooltip"

import PythonApi from "../apis/PythonApi"

//--Components
import PlotCard from '../components/PlotCard'
import SettingsContainer from '../components/settings/SettingsContainer'

//--Redux
import { connect } from 'react-redux'
import { addPlots, setNewStructures, setActivePlot, setCurrentSession } from "../redux/actions"
import { CHANGE_PLOT_SETTINGS } from '../redux/actions/actionTypes'



class PlotTweaking extends Component {

    componentDidMount(){

        PythonApi.getSessionSettings(session => this.props.setCurrentSession(session))
    }

    submitSettings = (settings) => {

        PythonApi.updatePlotSettings(this.props.active.plot.id, settings, (plot) => {
            this.props.addPlots( {[plot.id]: plot})

            if (this.props.active.plot.id == plot.id){
                this.props.setActivePlot(plot)
            }
            
        })
    }

    undoSettings = () => {

        PythonApi.undoPlotSettings(this.props.active.plot.id, (plot) => {
            this.props.addPlots( {[plot.id]: plot})

            if (this.props.active.plot.id == plot.id){
                this.props.setActivePlot(plot)
            }
            
        })
    }

    render() {

        if (! this.props.active.plot ) return null

        let setsCont = <SettingsContainer
                            settings={this.props.active.plot.settings}
                            params={this.props.active.plot.params}
                            paramGroups={this.props.active.plot.paramGroups}
                            onSettingChangeType={CHANGE_PLOT_SETTINGS}
                            submitSettings={this.submitSettings}
                            undoSettings={this.undoSettings}/>
        
        return (
            <div>
                <Row>
                    <Col className="s12 l4">
                        <PlotCard plot={this.props.active.plot} plotStyle={{height:"75vh"}}/>
                    </Col>
                    <Col className="s12 l8" >
                        {this.props.browser.mediaType == "infinity" ? <div className="scrollView" style={{ maxHeight: "90vh" }}>{setsCont}</div> : setsCont}
                    </Col>
                </Row>
                <ReactTooltip multiline disable={this.props.session.settings ? !this.props.session.settings.showTooltips : false}/>  
            </div>
            
        )
    }
}

const mapStateToProps = state => ({
    plots: state.plots,
    structures: state.structures,
    active: state.active,
    session: state.session,
    browser: state.browser
})

const mapDispatchToProps = dispatch => ({
    setCurrentSession: (session) => dispatch(setCurrentSession(session)),
    addPlots: (newPlots, tabsIDs) => dispatch(addPlots(newPlots, tabsIDs)),
    setActivePlot: (plot) => dispatch(setActivePlot(plot)),
    setNewStructures: (structures) => dispatch(setNewStructures(structures)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PlotTweaking);
