import React, { Component } from "react";

import _ from "lodash"

//--Components
import {
  SortableContainer,
  SortableElement,
  arrayMove
} from "react-sortable-hoc";
import PlotCard from "./PlotCard";
import { Col, Row} from "react-materialize";

//--Redux
import { connect } from 'react-redux'
import {addPlot, setTabPlots} from "../redux/actions"

const SortableItem = SortableElement(({ plot }) => (
  <Col s={12} m={6} l={4}>
    <PlotCard plot={plot} sortable plotStyle={{height: "65vh"}}/>
  </Col>
));

const SortableList = SortableContainer(({ plots }) => {
  return (
    <Row>
      {plots.map((plot, index) => (
        <SortableItem key={index} index={index} plot={plot} />
      ))}
    </Row>
  );
});

class PlotDashboard extends Component {

  onSortEnd = ({ oldIndex, newIndex }) => {
    this.props.setTabPlots(
      this.props.active.tab,
      arrayMove( _.find(this.props.tabs,["id", this.props.active.tab]).plots, oldIndex, newIndex)
    );
  };

  render() {

    if (!this.props.active.tab ) return null

    let tab = _.find(this.props.tabs, ["id", this.props.active.tab])

    if (!tab) return null
    
    let plots = tab.plots.map(plotID => this.props.plots[plotID])

    return (
      <div>
        <SortableList
          axis="xy"
          useDragHandle
          transitionDuration={0}
          plots={plots}
          onSortEnd={this.onSortEnd}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  plots: state.plots,
  structures: state.structures,
  tabs: state.tabs,
  active: state.active
})

const mapDispatchToProps = dispatch => ({
  setTabPlots: (tabID, plots) => dispatch(setTabPlots(tabID, plots)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PlotDashboard);