import React, { Component } from 'react'
import Plot from 'react-plotly.js';
import { Card, Row, Icon } from 'react-materialize';

import { CircleLoader} from 'react-spinners';
import ReactTooltip from "react-tooltip"
import {
    sortableHandle,
} from "react-sortable-hoc";

import _ from "lodash"
import {unflatten} from "flat"

//--Redux
import { connect } from 'react-redux'
import { setActivePlot, removePlot, setSessionTabs} from "../redux/actions"
import PythonApi from '../apis/PythonApi';

class PlotCard extends Component {

    constructor(props){
        super(props)
        this.state = {
            isLoading: false,
        }
    }

    removePlot = () => {
        PythonApi.removePlot({ plotID: this.props.plot.id, tabID: this.props.active.tab}, (session, reqParams) => {
            this.props.setSessionTabs(session.tabs)
            this.props.removePlot(reqParams.plotID)
        })
        
    }

    setPlotAsActive = () => {
        this.props.setActivePlot(this.props.plot)
    }

    showPlotFullScreen = () => {
        PythonApi.showPlotFullScreen(this.props.plot.id)
    }

    render() {

        if (this.state.isLoading || !this.props.plot) {
            return (
                <Card 
                    className="plotcard"
                    style={{borderRadius: 15}}>
                    <Row style={{textAlign: "center"}}>
                        <CircleLoader color="#36D7B7"/>
                    </Row>
                    <div>We are working hard to load your plot.</div>
                </Card>
            )
        }

        let layout = this.props.plot.figure.layout;
        let backColor = this.props.plot.settings.paper_bgcolor || "white"

        //If this is the active plot, we are going to overwrite the layout with the settings (so that user is able to see a preview)
        if (this.props.active.plot && this.props.active.plot.id == this.props.plot.id){

            backColor = this.props.active.plot.settings.paper_bgcolor || "white"

            // let layoutSubGroups = _.groupBy( _.groupBy(this.props.active.plot.params, "group")["layout"] , "subGroup")

            // layout = {
            //     ...layout,
            //     ...layoutSubGroups["undefined"].reduce((map, param) => {map[param.key] = this.props.active.plot.settings[param.key]; return map}, {} ),
            //     "xaxis": {
            //         ...layout.xaxis,
            //         ...layoutSubGroups["xaxis"].reduce((map, param) => {map[param.key.split("_").pop()] = this.props.active.plot.settings[param.key]; return map}, {}),
            //     },
            //     "yaxis": {
            //         ...layout.yaxis,
            //         ...layoutSubGroups["yaxis"].reduce((map, param) => {map[param.key.split("_").pop()] = this.props.active.plot.settings[param.key]; return map}, {} ),
            //     }

            // }

            let layoutParams = _.groupBy(this.props.active.plot.params, "group")["layout"]

            let layoutSettings = layoutParams.reduce((map,param) => {map[param.key] = this.props.active.plot.settings[param.key]; return map},{})

            let nestedLayoutParams = unflatten( layoutSettings, {delimiter: "_"})


            layout = {
                ...layout,
                ...layoutSettings,
                "xaxis": {
                    ...layout.xaxis,
                    ...nestedLayoutParams.xaxis
                },
                "yaxis": {
                    ...layout.yaxis,
                    ...nestedLayoutParams.yaxis
                }

            }
            
        }
        
        const DragHandle = sortableHandle(() => <div style={{cursor: "pointer"}}><Icon className="small grey-text" >drag_handle</Icon></div>);

        const dragHandle = this.props.sortable ? (
            <div
                data-tip="Drag the plot to change its position"
                style={{justifyContent: "center", alignItems: "center", textAlign: "center", paddingTop: "20px", background: backColor}}>
                <DragHandle/>
            </div>) : null;

        return (
            
            <Card
                className="plotcard"
                style={{borderRadius: 15, overflow: "hidden"}}>
                <div style={{display: "flex", flexDirection: "column", ...this.props.style}}>
                    {dragHandle}
                    <div style={{flex:1, padding: "10px 10px 0 10px", background: backColor}}>
                        <Plot
                            useResizeHandler
                            style={{ width: "auto", height: "100%", ...this.props.plotStyle}}
                            data={this.props.plot.figure.data}
                            layout={layout}
                            frames={this.props.plot.figure.frames}
                            onClick={(data) => console.log(data)}
                            onUpdate={(figure) => console.warn(figure)}
                        />
                    </div>
                    <div style={{display: "flex"}} className="card-action">
                        <div style={{flex:1, textAlign: "left"}}>
                            <a data-tip="Full screen" href="#" onClick={this.showPlotFullScreen}><Icon>fullscreen</Icon></a> 
                        </div>

                        <div>
                            <a
                                data-tip="Edit"
                                href="/plotTweaking"
                                onClick={this.setPlotAsActive}><Icon>edit</Icon></a>
                            <a data-tip="Export data" href="#"><Icon>file_download</Icon></a>
                            <a data-tip="Remove" href="#" className="red-text" onClick={this.removePlot}><Icon className="removePlot">delete</Icon></a>
                        </div>
                        
                    </div>  
                </div>
                <ReactTooltip multiline disable={this.props.session.settings ? !this.props.session.settings.showTooltips : false}/>
            </Card>
        )
    }
}

const mapStateToProps = state => ({
    active: state.active,
    session: state.session
})

const mapDispatchToProps = dispatch => ({
    setSessionTabs: (tabs) => dispatch(setSessionTabs(tabs)),
    setActivePlot: (plot) => dispatch(setActivePlot(plot)),
    removePlot: (plotID, tabID) => dispatch(removePlot(plotID, tabID)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PlotCard);
