import { CLEAR_SESSION, SET_SESSION_TABS} from '../actions/actionTypes'
import _ from "lodash"

const defaultState = []

const tabs = (state = defaultState, action) => {

    switch (action.type) {
        case SET_SESSION_TABS:
            console.log("REDUX: Setting session tabs...")
            var newState = action.tabs
            console.log(newState)

            return newState
        
        case CLEAR_SESSION:
            return defaultState

        default:
            return state
    }
}

export default tabs