
//This class will contain all the fetches to the python API
class PythonApi {

    constructor(){

        this.apiAdress = 'http://localhost:4000'
    }

    //ADDRESSES
    get sessionAdress(){
        return "/sislGUI/session"
    }

    get tabsAdress(){
        return "/sislGUI/tab"
    }

    get newtabAdress(){
        return this.tabsAdress + "/new"
    }

    tabAdress = (tabID) => {
        return this.tabsAdress + "/" + tabID
    }

    get plotsAdress(){
        return "/sislGUI/plot"
    }

    plotAdress = (plotID) => {
        return this.plotsAdress + "/" + plotID
    }

    get plotTypesAdress(){
        return "/sislGUI/plotTypes"
    }

    get structsAdress(){
        return "/sislGUI/structs"
    }

    //MOST LOW LEVEL METHODS (Should not be used directly)
    _requestToApi = (adress, request, responseHandler ) => {

        fetch(this.apiAdress + adress, 
      {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        ...request
    })
      .then(response => response.json())
      .then(response => {
        if (response.statusCode >= 400){
            alert(response.error)
        } else {

            //The plot figure comes double JSON encoded (Plotly encoding + flask encoding)
            if (response.plot){

                response = {
                    ...response,
                    plot: {
                        ...response.plot,
                        figure: JSON.parse(response.plot.figure)
                    }
                }
            }

            responseHandler(response)
        }
      })
      .catch((error) => {
          console.log(error)
          //alert("Can not connect to the API, are you sure it is running? If so, check your API adress in settings.")
      })
    }

    _get = (adress, {request, responseHandler}) => {
        this._requestToApi(adress, {...request, method: 'GET'}, responseHandler)
    }

    _post = (adress, {request, responseHandler}) => {
        this._requestToApi(adress, {...request, method: 'POST'}, responseHandler)
    }

    _put = (adress, {request, responseHandler}) => {
        this._requestToApi(adress, {...request, method: 'PUT'}, responseHandler)
    }

    _delete = (adress, {request, responseHandler}) => {
        this._requestToApi(adress, {...request, method: 'DELETE'}, responseHandler)
    }

    //A BIT HIGHER LEVEL METHODS (to be used by high level methods below)
    _updateSettings = (adress, newSettings, callback = () => {}) => {
        this._post(adress, {request: {body: JSON.stringify({settings: newSettings, action: "updateSettings"})}, responseHandler: callback})
    }

    _undoSettings = (adress, callback = () => {}) => {
        this._post(adress, {request: {body: JSON.stringify({action: "undoSettings"})}, responseHandler: callback})
    }

    _save = (adress, params, callback = () => {}) => {
        this._post(adress, {request: {body: JSON.stringify({action: "save", ...params})}, responseHandler: callback})
    }

    _load = (adress, params, callback = () => {}) => {
        this._post(adress, {request: {body: JSON.stringify({action: "load", ...params})}, responseHandler: callback})
    }


    //HIGH LEVEL METHODS (should be used by components)

    getSessionSettings = (callback = () => {}) => {
        this._get(this.sessionAdress, {responseHandler: (response) => callback(response.session)} )
    }

    updateSessionSettings = ( sessionID, newSettings, callback = () => {}) => {
        this._updateSettings(this.sessionAdress, newSettings, (response) => callback(response.session))
    }

    undoSessionSettings = ( sessionID, callback = () => {}) => {

        this._undoSettings(this.sessionAdress, (response) => callback(response.session))
    }

    saveSession = (params, callback = () => {}) => {
        this._save(this.sessionAdress, params, (response) => callback(response.session, params))
    }

    loadSession = (params, callback = () => {}) => {
        this._load(this.sessionAdress, params, (response) => callback(response.session, params))
    }

    getAvailableStructs = (callback = () => {}) => {

        this._get(this.structsAdress, {responseHandler: (response) => callback(response.structures)} )
        
    }

    getSessionTabs = (callback = () => {}) => {

        this._get(this.tabsAdress, {responseHandler: (response) => callback(response.tabs)} )
        
    }

    addNewTab = (callback = () => {}) => {

        this._get(this.newtabAdress, {responseHandler: (response) => callback(response.tabs)} )
        
    }

    removeTab = (tabID, callback = () => {}) => {

        this._delete(this.tabAdress(tabID), {responseHandler: (response) => callback(response.tabs)} )
        
    }

    updateTabParams = (tabID, newTabParams, callback = () => {}) => {

        this._put(this.tabAdress(tabID), {request: {body: JSON.stringify(newTabParams)}, responseHandler: (response) => callback(response.tabs)} )
        
    }

    getPlotOptions = (callback = () => {}) => {
        this._get(this.plotTypesAdress, {responseHandler: (response) => callback(response.plotOptions)} )
    }

    getPlot = (plotID, callback = () => {}) => {

        this._get(this.plotAdress(plotID), {responseHandler: (response) => callback(response.plot)})
    }

    getNewPlot = (params, callback = () => {}) => {

        this._post(this.plotsAdress, {request: {body: JSON.stringify(params)}, responseHandler: (response) => callback(response.plot, params)})
    }

    updatePlotSettings = ( plotID, newSettings, callback = () => {}) => {

        this._updateSettings(this.plotAdress(plotID), newSettings, (response) => callback(response.plot))
    }

    undoPlotSettings = ( plotID, callback = () => {}) => {

        this._undoSettings(this.plotAdress(plotID), (response) => callback(response.plot))
    }

    removePlot = (params, callback = () => {}) => {

        this._delete(this.plotAdress(params.plotID), { responseHandler: (response) => callback(response.session,params)})
    }

    showPlotFullScreen = ( plotID, callback = () => {}) => {
        this._post(this.plotAdress(plotID), {request: {body: JSON.stringify({action: "fullScreen"})}, responseHandler: (response) => callback(response.plot)})
    }

}

export default new PythonApi();