import { SET_STRUCTURES, CLEAR_SESSION } from '../actions/actionTypes'

const defaultState = {}

const structures = (state = defaultState, action) => {
    switch (action.type) {
        case SET_STRUCTURES:
            console.log("REDUX: Setting the new structures...")
            let newState = {
                ...action.newStructures
            }
            console.log(newState)
            return newState

        default:
            return state
    }
}

export default structures